/* 页面 汇总提示样式 tipArea */
.tipArea {
    width: 100%;
    height: 49px;
    background: #fff;
    border-bottom: 1px solid #d4d4d4;
    line-height: 49px;
    .span {
        margin-left: 30px;
        color: #3d3d3d;
        font-size: 18px;
        em {
            margin-left: 6px;
            &.blue {
                color: #50d2c2;
            }
        }
    }
}
.tipArea .span em.red,
b.red {
    color: #c1272d;
}
.tipArea .span em.orange {
    color: #ffb258;
}
.tipArea a.ta {
    display: block;
    width: 70px;
    height: 30px;
    -moz-border-radius: 3px;
         border-radius: 3px;
    background: #50d2c2;
    text-align: center;
    line-height: 30px;
    font-size: 18px;
    color: #fff;
    margin: 10px 20px 0 0;
}
.tipArea a.ta:hover {
    background: #4bc8b9;
}
/* 页面 内容部分样式 contents */
.contents {
    width: 99%;
    /* height: 100%;*/
    overflow: hidden;
    position: relative;
    margin-bottom: 20px;
    border: 1px solid #eaeaea;
    -moz-border-radius: 3px;
         border-radius: 3px;
    background: #f7f8f8;
    -moz-box-shadow: 0 0 2px 2px #eeeeee;
         box-shadow: 0 0 2px 2px #eeeeee;
    margin-top: 10px;
}
