.choose-members {
    margin: 0 10px;
    padding-bottom: 20px;
    overflow: hidden;
    li {
        width: 48px;
        margin: 12px 5px 0;
        text-align: center;
        float: left;
        a {
            display: block;
            position: relative;
            img {
                width: 40px;
                height: 40px;
                margin: 4px;
                -moz-border-radius: 24px;
                border-radius: 24px;
            }
            em {
                font-size: 12px;
                color: #383838;
                overflow: hidden;
                display: block;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            &.current, &:hover {
                img {
                    border: 4px solid #96dfd6;
                    margin: 0;
                }
                em {
                    color: #707070;
                }
            }
            label{
                position: absolute;
                display: inline-block;
                top:28px;
                right:-3px;
                width:20px;
                height:20px;
                background: url("../images/phone.png");
                background-size: contain;
                background-position: center center;
                background-repeat: no-repeat;
            }
        }
    }
}

