/*签到的查看图片 轮播*/
$htmlfontsize: 75;
@function rem($number) {
    @return ($number/$htmlfontsize)*1rem;
}
/*--------单位-----------*/
@mixin font-size($font-size) {
    font-size: $font-size;
    [data-dpr="2"] & {
        font-size: $font-size * 2;
    }
    [data-dpr="3"] & {
        font-size: $font-size * 3;
    }
}
/*----------字体--------*/
/*是否支持某个浏览器的前缀，如果你不想支持，可以设置为false*/
/*----------------------------*/
$prefix-for-webkit: true !default;
$prefix-for-mozilla: true !default;
$prefix-for-microsoft: true !default;
$prefix-for-opera: true !default;
$prefix-for-spec: true !default; /* 标准版*/
/* prefixer*/
/*----------------------------*/
@mixin prefixer ($property, $value, $prefixes) {
    @each $prefix in $prefixes {
        @if $prefix == webkit and $prefix-for-webkit == true {
            -webkit-#{$property}: $value;
        }
        @else if $prefix == moz and $prefix-for-mozilla == true {
            -moz-#{$property}: $value;
        }
        @else if $prefix == ms and $prefix-for-microsoft == true {
            -ms-#{$property}: $value;
        }
        @else if $prefix == o and $prefix-for-opera == true {
            -o-#{$property}: $value;
        }
        @else if $prefix == spec and $prefix-for-spec == true {
            #{$property}: $value;
        }
        @else {
            @warn "Unrecognized prefix: #{$prefix}";
        }
    }
}
@mixin compatible($style, $value) {
    @include prefixer($style, $value,spec webkit moz); /*  */
    /*  #{$style}: $value;*/
    /*  -webkit-#{$style}: $value;*/
    /*  -moz-#{$style}: $value;*/
    /*  -ms-#{$style}: $value;*/
}
@mixin borderradius($width) {
    @include compatible(border-radius,$width);
}
@mixin boxshadow($args) {
    @include compatible(box-shadow,$args);
}
@mixin animation($args) {
    @include compatible(animation,$args);
}
@mixin transform($value) {
    @include compatible(transform,$value);
}
@mixin opacity($value) {
    @include compatible(opacity,$value);
}
/*定义animation的keyframes*/
@mixin keyframes($name) {
    @-moz-keyframes #{$name} {
        @content;
    }
    @keyframes #{$name} {
        @content;
    }
    @-webkit-keyframes #{$name} {
        @content;
    }
}
/*-----------兼容-------------*/
@mixin clearfix {
    &:after {
        display: table;
        content: "";
        clear: both;
    }
}
$lte7: true;
%clearfix {
    @if $lte7 {
        *zoom: 1;
    }
    &:after,
    &:before {
        content: "";
        display: table;
        font: 0/0 a;
    }
    &:after {
        clear: both;
    }
}
/*--------cleafix---------*/
@mixin calc($style, $percentage, $width) {
    #{$style}: -moz-calc(#{$percentage} - #{$width}) !important;
    #{$style}: calc(#{$percentage} - #{$width}) !important;
}
/*-----------计算---------------*/
@mixin center() {
    display: block;
    margin-left: auto;
    margin-right: auto;
}
/*--------div居中---------*/
@mixin position($position, $args) {
    position: $position;
    $offsets: top right bottom left;
    @each $o in $offsets {
        $i: index($args, $o);
        @if $i and $i + 1 <= length($args) and type-of( nth($args, $i + 1) ) == number {
            #{$o}: nth($args, $i + 1);
        }
    }
}
@mixin absolute($args) {
    @include position(absolute, $args);
}
@mixin fixed($args) {
    @include position(fixed, $args);
}
@mixin relative($args) {
    @include position(relative, $args);
}
/*-------定位----------*/
/*定义media-queries的最小最大宽度*/
@mixin screen($res-min, $res-max) {
    @media screen and ( min-width: $res-min ) and ( max-width: $res-max ) {
        @content;
    }
}
/*定义所有不支持圆角的浏览器使用背景图片*/
/*得使用[modernizr](http://modernizr.com/)来检测，在html上加class*/
@mixin no-border-radius {
    .no-border-radius {
        @content
    }
}
/*先来个我们常用的用border生成三角形的@mixin:*/
/* triangle*/
@mixin triangle($direction, $size, $borderColor ) {
    content: "";
    height: 0;
    width: 0;
    @if $direction == top {
        border-bottom: $size solid $borderColor;
        border-left: $size dashed transparent;
        border-right: $size dashed transparent;
    }
    @else if $direction == right {
        border-left: $size solid $borderColor;
        border-top: $size dashed transparent;
        border-bottom: $size dashed transparent;
    }
    @else if $direction == bottom {
        border-top: $size solid $borderColor;
        border-left: $size dashed transparent;
        border-right: $size dashed transparent;
    }
    @else if $direction == left {
        border-right: $size solid $borderColor;
        border-top: $size dashed transparent;
        border-bottom: $size dashed transparent;
    }
}
